import styled from 'styled-components';
import { Link as LinkR } from 'react-router-dom';
import { Link as LinkS } from 'react-scroll';


export const Nav = styled.nav`
    background: white;
    height: 80px;
    margin-top: -80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.8rem;
    position: sticky;
    top: 0;
    z-index: 10;

        @media screen and (max-width: 960px){
            transition: 0.8s all ease;
        }
    `;
    
    export const NavbarContainer = styled.div`
    display: flex;
    justify-content: space-between;
    height: 80px;
    z-index: 1;
    width: 100%;
    padding: 0 24px;
    max-width: 1100px; 
    `;

    export const NavLogo = styled(LinkR)`
    color: black;
    justify-self: flex-start;
    cursor: pointer;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    margin-left: 24px;
    margin-right: 24px;
    font-weight: bold;
    text-decoration: none;
    `;

    export const MobileIcon = styled.div`
    display: none;

    @media screen and (max-width: 860px){
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
        color: black;
    }
    `;

    export const NavMenu = styled.ul`
    display: flex;
    align-items: center;
    list-style: none;
    margin-right: -22px;


    @media screen and (max-width: 860px){
        display: none;
    }
    `;

    export const NavItem = styled.li `
    height: 80px;
    `
    

    export const NavLinks = styled(LinkS)`
    color: black;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;

    &.active {
        border-bottom: 3px solid #01bf71;
    }
    `;

    export const NavBtn = styled.nav`
    display: flex;
    align-items: center;

    @media screen and (max-width: 860px) {
        display: none;
    }
    ` 

    export const NavBtnLink = styled(LinkR)`
    border-radius: 50px;
    border-width: 2px;
    background: transparent;
    white-space: nowrap;
    padding: 9px 22px;
    color: black;
    font-size: 15px;
    outline: none;
    border-style: solid;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;

    &:hover{
        transition: all 0.1s ease-in-out;
        background: #3593F1;
        color: #fff;
        
        
    }
    `;